/* eslint-disable */
import React, { useState, useEffect, useRef } from "react"
import { connect } from "react-redux"
import { Button, Modal } from 'antd'
import Message from "./Message"
import NewMessageField from "./NewMessageField"
import { openDialog, toggleCryptoMode, forwardingMessage } from "../actions/dialogs"
import socket from "../transport/Connect"
import axios from 'axios'
import { ui } from "../reducers/ui"
import helpers from "../helpers"
import BigNumber from "bignumber.js"
import Crypto from "y_crypto"
import { setCallbackId } from "../actions/callbacks"
import { setSymmetricKey, setSymmetricKeyForDecrypt, setPublicKeys } from "../actions/keys"
import { CloseOutlined, ExclamationCircleOutlined } from '@ant-design/icons';
import JSONbig from "json-bigint"

function Dialog(props) {
  // const [messages, setMessages] = useState([])
  const [dialog, setDialog] = useState(null)
  const [isCrypto, setIsCrypto] = useState(false)
  const [messagesLoading, setMessagesLoading] = useState(false)
  const [lastMessage, setLastMessage] = useState(null)
  const [callbackFunctions, setCallbackFunctions] = useState([])
  const [interlocutorEncryptPublicKey, setInterlocutorEncryptPublicKey] = useState(null)
  const mesViewport = useRef(null)

  // function for receiving sender in messages and forwarded messages
  // const getUsers = mess => {
  //   const users = []
  //   mess.forEach(message => {
  //     if (message.Attachments) {
  //       if (message.Attachments.length) {
  //         if (message.Attachments[0].Type === 5) {
  //           if (message.Attachments[0].Payload.length > 0) {
  //             const foundUser = props.users.find(
  //               user => user.Id === message.Attachments[0].Payload[0].SenderId
  //             )

  //             if (!foundUser) {
  //               if(message.Attachments[0].Payload[0].SenderId){
  //                 users.push(message.Attachments[0].Payload[0].SenderId)
  //               }
  //             }
  //           }
  //         }
  //       }
  //     }
  //     const foundUser = props.users.find(user => user.Id === message.SenderId)

  //     if (!foundUser) {
  //       if(message.SenderId){
  //         users.push(message.SenderId)
  //       }
  //     }
  //   })
  //   const uniqueUsers = [...new Set(users.map(n => n))]

  //   if (uniqueUsers.length) {
  //     const requestUsers = {
  //       UsersId: [...uniqueUsers],
  //       RequestType: 28,
  //       RequestId: socket.makeId(),
  //       Type: 0
  //     }

  //     socket.send(requestUsers)
  //   }
  // }

  useEffect(() => {
    if (dialog && dialog.SecondUid) {
      const requestUser = {
        UsersId: [dialog.SecondUid],
        RequestType: 28,
        RequestId: socket.makeId(),
        Type: 0
      }

      socket.send(requestUser)
    }
  }, [dialog])

  useEffect(() => {
    if (isCrypto) {
      // receiving public encryption keys of the interlocutor
      const requestId = socket.makeId()
      props.setCallbackId(requestId)
      let callbackIds = callbackFunctions
      callbackIds.push(requestId)
      setCallbackFunctions(callbackIds)

      if (props.user && dialog) {
        const getUserKeys = {
          UserId: dialog.SecondUid,
          Direction: true,
          KeysId: null,
          RequestType: 105,
          NavigationTime: parseInt(new Date().getTime()/1000),
          RequestId: requestId,
          Type: 0
        }
        socket.send(getUserKeys)
      }
    }
  }, [isCrypto])

  useEffect(() => {
    if (dialog) {
      if (dialog.isCrypto != isCrypto) {
        setIsCrypto(dialog.isCrypto)
      }
    }
  }, [props.dialogs])

  const confirm = (text) => {
    Modal.error({
      title: "",
      icon: <ExclamationCircleOutlined />,
      content: text,
      okText: "",
      cancelText: ""
    })
  }

  useEffect(() => {
    props.callbacks.forEach(callback => {
      if (callbackFunctions.includes(callback.id) && callback.data) {
        setCallbackFunctions([])
        const encoder = new TextEncoder()
        const masterPassword = encoder.encode('qwerty')
        let masterPasswordId = BigNumber(1)
        const crypto = new Crypto(masterPassword, masterPasswordId)

        if (callback.data.Keys[0]) {
          let keyTimelife = (callback.data.Keys[0].GenerationTime + callback.data.Keys[0].Lifetime)*1000
          let date = new Date()
          if (date.getTime()/1000 < keyTimelife) {
            // console.log("  ,  ")
            // Type = 0 - sign
            // Type = 1 - encrypt
            let interEncryptPublicKey = callback.data.Keys[0].Type === 1 ? callback.data.Keys[0] : callback.data.Keys[1]
            setInterlocutorEncryptPublicKey(interEncryptPublicKey)
            let mySignPrivateKey = JSON.parse(localStorage.getItem("signKeysPair"))[JSON.parse(localStorage.getItem("signKeysPair")).length-1]
            const keyId = socket.makeId()
            crypto.setPublicEncryptKeyToSend(helpers.base64ToBuffer(interEncryptPublicKey.Data))
            crypto.setPrivateSignKeyToSend(helpers.base64ToBuffer(mySignPrivateKey.private))
            crypto.getSymmetricKey(1, keyId, 99999, ).then(symmetricKey => {
              // add this unencrypted symmetric key to the state to decrypt messages on it
              let symmKey = {
                key: helpers.bufferToBase64(symmetricKey),
                idKey: keyId
              }
              props.setSymmetricKeyForDecrypt(symmKey)
              crypto.encryptKeyMsg(1, 99999, symmetricKey).then(encryptedSymmetricKey => {
                // console.log(encryptedSymmetricKey)
                // here we add the encrypted symmetric key to the state
                // This key is sent to the interlocutor at the beginning of a secure dialogue, and an identifier is sent from it when sending messages
                let symmKeyEncrypted = {
                  key: helpers.bufferToBase64(symmetricKey),
                  id: keyId
                }
                props.setSymmetricKey(symmKeyEncrypted)
              }).catch(err => {
                props.toggleCryptoMode({dialog: null, isCrypto: false})
                confirm(err.message)
                console.log(err)
              })
            }).catch(err => {
              props.toggleCryptoMode({dialog: null, isCrypto: false})
              confirm(err.message)
              console.log(err)
            })
          } else {
            props.toggleCryptoMode({dialog: null, isCrypto: false})
            confirm(err.message)
            // console.log("   ")
          }
        } else {
          props.toggleCryptoMode({dialog: null, isCrypto: false})
          confirm(err.message)
          // console.log("     ")
        }
      }
    })
  }, [props.callbacks])

  useEffect(() => {
    if (props.user && props.dialog) {
      const requestId = socket.makeId()
      const keysPairs = JSON.parse(localStorage.getItem('keysPair'))
      const signKeysPairs = JSON.parse(localStorage.getItem('signKeysPair'))
      props.setCallbackId(requestId)
      
      const addNewKeys = {
        Keys: [
          {
            KeyId: keysPairs[keysPairs.length - 1].id,
            Data: keysPairs[keysPairs.length - 1].public, 
            Lifetime: 99999,
            GenerationTime: parseInt(new Date().getTime()/1000),
            Version: 1,
            // type 0 - signature, 1 - encryption
            Type: 1,
            UserId: props.user.Id,
            ChatId: null
          },
          {
            KeyId: signKeysPairs[keysPairs.length - 1].id,
            Data: signKeysPairs[keysPairs.length - 1].public, 
            Lifetime: 99999,
            GenerationTime: parseInt(new Date().getTime()/1000),
            Version: 1,
            // type 0 - signature, 1 - encryption
            Type: 0,
            UserId: props.user.Id,
            ChatId: null
          }
        ],
        RequestType: 100,
        RequestId: requestId,
        Type: 0
      }

      socket.send(addNewKeys)
    }
  }, [props.user, props.dialog])

  useEffect(() => {
    if(props.dialog){
      setDialog(props.dialog)

      // mark messages read
      if (props.appFocus) {
        if (props.dialog.LastMessageId) {
          const data = {
            MessagesId: [props.dialog.LastMessageId],
            ConversationType: props.dialog.ConversationType,
            ConversationId: props.dialog.ConversationId,
            RequestId: socket.makeId(),
            RequestType: 35,
            Type: 0
          }
          
          socket.send(data)
        }
      }

      // scrolling message loading
      mesViewport.current.onscroll = () => {
        if (mesViewport.current.scrollTop === 0 && props.messages[0]) {
          setMessagesLoading(true)
          if (document.querySelectorAll('.message')[0]) {
            setLastMessage(document.querySelectorAll('.message')[0].getAttribute('id'))
          }

          const messagesRequest = {
            ConversationType: props.dialog.ConversationType,
            ConversationId: props.dialog.ConversationId,
            NavigationMessageId: props.messages[0].GlobalId,
            MessagesId: null,
            RequestType: 25,
            RequestId: socket.makeId(),
            Direction: true,
            Type: 0
          }

          socket.send(messagesRequest)
        }
      }
    }
  }, [props.messages, props.dialog])

  useEffect(() => {
    if (dialog) {
    //   setTimeout(() => {
        if (mesViewport.current && !messagesLoading) {
          setTimeout(() => {
            mesViewport.current.scrollTop = 450000
          }, 1)
          setMessagesLoading(false)
        } else {
          if(lastMessage){
            // console.log(document.getElementById('1f88a55d-1e18-b31f-27cd-572f261c2009'))
            setMessagesLoading(false)
            setLastMessage(null)
            // 70 - hight of head
            let lastMessageOffset = document.getElementById(lastMessage).offsetTop - 70
            mesViewport.current.scrollTop = lastMessageOffset - 20

            // document.getElementById(lastMessage).scrollIntoView()
          }
        }
    //   }, 1)

    //   getUsers(mess)
    }
  }, [dialog, props.messages])

  useEffect(() => {
    // send a symmetric key to the interlocutor and add interlocutor key in state
    if (props.symmetricKey) {
      // console.log('  ')
      const keyExchangeMessage = {
        Messages: [
          {
            Id: 0,
            SendingTime: "",
            SenderId: props.user.Id,
            ReceiverId: props.dialog.SecondUid,
            ConversationId: props.dialog.ConversationId,
            ConversationType: props.dialog.ConversationType,
            Read: false,
            NodesId: null,
            ReplyTo: null,
            Text: null,
            Attachments: [
              {
                Type: 6,
                Hash: null,
                MessageId: 0,
                Payload: {
                  KeyId: interlocutorEncryptPublicKey.KeyId,
                  SignKeyId: JSON.parse(localStorage.getItem("signKeysPair"))[JSON.parse(localStorage.getItem("signKeysPair")).length-1].id,
                  EncryptedData: props.symmetricKey.key
                },
                NodesId: null
              }
            ],
            GlobalId: null
          }
        ],
        RequestType: 39,
        RequestId: socket.makeId(),
        Type: 0
      }
  
      socket.send(keyExchangeMessage)
    }
  }, [props.symmetricKey])

  const joinToChannel = () => {
    const data = {
      ChannelsId: [dialog.ChannelId],
      UsersId: [props.user.Id],
      RequestType: 48,
      RequestId: socket.makeId(),
      Type: 0
    }

    socket.send(data)
  }

  const fileOver = e => {
    e.preventDefault()
    mesViewport.current.classList.add('file-over')
  }

  const fileLeave = e => {
    e.preventDefault()
    mesViewport.current.classList.remove('file-over')
  }

  const fileDrop = (e) => {
    e.preventDefault()
    mesViewport.current.classList.remove('file-over')

    if (props.isCrypto) {
      // encrypted message with file
      const encoder = new TextEncoder()
      const masterPassword = encoder.encode('qwerty')
      let masterPasswordId = BigNumber(1)
      const crypto = new Crypto(masterPassword, masterPasswordId)

      crypto.setSymmetricKey(helpers.base64ToBuffer(props.symmetricKey.key))
      // crypto.encryptMsg(1, 4, 99999, encoder.encode(e.dataTransfer.files[0])).then(data => {
        // if (data.length > 0) {
          const fileAccessToken = localStorage.getItem('FileAccessToken')
          axios.defaults.headers['FileAccessToken'] = fileAccessToken

          const formData = new FormData()
          // formData.append('file', new Blob(data))
          formData.append('file', e.dataTransfer.files[0])

          axios.post(`https://${props.node}:5000/api/Files`, formData).then(upladedFile => {
            // console.log(upladedFile)
            crypto.encryptMsg(1, 4, 99999, encoder.encode(JSON.stringify(upladedFile.data.File))).then(encryptImgMsg => {
              const data = {
                Messages: [
                  {
                    Id: 0,
                    SendingTime: "",
                    SenderId: props.user.Id,
                    ReceiverId: dialog.SecondUid,
                    ConversationId: dialog.ConversationId,
                    ConversationType: dialog.ConversationType,
                    Read: false,
                    NodesId: null,
                    ReplyTo: null,
                    Text: null,
                    Attachments: [
                      {
                        Type: 4,
                        Hash: null,
                        MessageId: 0,
                        Payload: {
                          KeyId: props.symmetricKey.id,
                          SignKeyId: JSON.parse(localStorage.getItem("signKeysPair"))[JSON.parse(localStorage.getItem("signKeysPair")).length-1].id,
                          SaveFlag: 1,
                          Lifetime: 99999,
                          EncryptedData: helpers.bufferToBase64(encryptImgMsg)
                        },
                        NodesId: null
                      }
                    ],
                    GlobalId: null
                  }
                ],
                RequestType: 39,
                RequestId: socket.makeId(),
                Type: 0
              }
              
              // console.log(data)
              socket.send(data)
            }).catch(err => {
              // console.log(err)
            })
          }).catch(err => {
            // console.log(err)
          })
        // }
      // }).catch(err => {
      //   console.log(err)
      // })
    } else {
      // regular message with file
      const isJpgOrPng = e.dataTransfer.files[0].type === "image/jpeg" || e.dataTransfer.files[0].type === "image/png"
      let attachmentType = null
      if (isJpgOrPng) {
        attachmentType = 2
      } else {
        attachmentType = 1
      }

      const fileAccessToken = localStorage.getItem('FileAccessToken')
      axios.defaults.headers['FileAccessToken'] = fileAccessToken

      const formData = new FormData()
      formData.append('file', e.dataTransfer.files[0])

      axios.post(`https://${props.node}:5000/api/Files`, formData)
      .then(
          (res) => {
            if (res.status === 200) {
              // setImgLoading(false)
              const data = {
                Messages: [
                  {
                    Id: 0,
                    SendingTime: "",
                    SenderId: props.user.Id,
                    ReceiverId: dialog.SecondUid,
                    ConversationId: dialog.ConversationId,
                    ConversationType: dialog.ConversationType,
                    Read: false,
                    NodesId: null,
                    ReplyTo: null,
                    Text: "",
                    Attachments: [
                      {
                        Type: attachmentType,
                        Hash: null,
                        MessageId: 0,
                        Payload: res.data.File.FileId,
                        NodesId: null
                      }
                    ],
                    GlobalId: null
                  }
                ],
                RequestType: 39,
                RequestId: socket.makeId(),
                Type: 0
              }
        
              socket.send(data)
            }
          }
      )
      .catch(
          (err) => {
            // console.log(err)
          }
      )
    }
  }

  return (
    <div className="dialog">
      <div 
        className="dialog__messages-viewport"
        ref={mesViewport}
        onDragOver={(e) => fileOver(e)}
        onDragLeave={(e) => fileLeave(e)}
        onDrop={(e) => fileDrop(e)}
      >
        <div className="dialog__messages">
          {dialog
            ? props.messages.filter(message => message.ConversationId === parseInt(dialog.ConversationId || dialog.ChannelId) && message.ConversationType === dialog.ConversationType).map(message => {
                return <Message key={socket.makeId()} message={message} />
              })
            : null}
        </div>
      </div>
      {props.forwardMessage ? (
        <div className="reply-message">
          <CloseOutlined onClick={() => {props.forwardingMessage(false)}} style={{color: '#1890FF', paddingRight: 10, cursor: 'pointer'}} />
           :
          {` ${props.forwardMessage.Text}`}
        </div>
      ) : null}
      {
        props.dialog ? 
          props.dialog.type === "channel" && props.dialog.fake ? 
            <Button type="primary" size='large' onClick={joinToChannel}></Button> 
          : props.dialog.ConversationType !== 3 ?
            <NewMessageField {...props} dialog={dialog} />
          : null
        : null
      }
    </div>
  )
}

function mapStateToProps(state) {
  // get messages only for this dialog
  // let dialog = state.dialogs.find(dialog => dialog.active)
  // const mess = state.messages.filter(message => message.ConversationId === parseInt(dialog.ConversationId || dialog.ChannelId) && message.ConversationType === dialog.ConversationType)
  return {
    messages: state.messages,
    forwardMessage: state.forwardMessage,
    users: state.users,
    user: state.users.find(user => user.currentUser === true),
    node: state.node.activeNode.Domains[0],
    appFocus: state.ui.appFocus,
    requestsId: state.ui.requestsId,
    symmetricKey: state.symmetricKey,
    callbacks: state.callbacks,
    dialogs: state.dialogs
  }
}

const mapDispatchToProps = dispatch => {
  return {
    openDialog: dialogId => dispatch(openDialog(dialogId)),
    forwardingMessage: data => dispatch(forwardingMessage(data)),
    setCallbackId: id => dispatch(setCallbackId(id)),
    setSymmetricKey: key => dispatch(setSymmetricKey(key)),
    setSymmetricKeyForDecrypt: key => dispatch(setSymmetricKeyForDecrypt(key)),
    toggleCryptoMode: (data) => dispatch(toggleCryptoMode(data)),
    setPublicKeys: publicKeys => dispatch(setPublicKeys(publicKeys)),
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Dialog)
