import {useEffect} from "react"
import { connect } from "react-redux"
import Connect from "./Connect"
import Request from "./Request"
import {
  dialogsReceived,
  chatsReceived,
  messagesReceived,
  openDialog,
  readMessages,
} from "../actions/dialogs"
import { toggleModal, toggleDrawer, socketReconnect } from "../actions/ui"
import { setCallbackData } from "../actions/callbacks"
import { searchResultReceived } from "../actions/search"
import {
  setContactUsersIDs,
  userGroupsReceived,
  setContacts
} from "../actions/contacts"
import selectors from "../selectors"
import { message } from "antd"
import { userReceived, chatsUsersReceived } from "../actions/user"
import { setAuth } from "../actions/auth"
import helpers from "../helpers"
import Crypto from "y_crypto"
import { setSymmetricKey, setPublicKeys } from "../actions/keys"
import JSONbig from "json-bigint"

function getUserKeys(messages) {
  let keysId = []

  messages.forEach(message => {
    if (message.Attachments) {
      if (message.Attachments[0]) {
        if (message.Attachments[0].Type === 6) {
          // TODO    
          // console.log(message)
          // keysId.push(message.Attachments[0].Payload.SignKeyId.toString())
          const userKeyRequest = {
            UserId: message.SenderId,
            Direction: true,
            KeysId: [message.Attachments[0].Payload.SignKeyId.toString()],
            RequestType: 105,
            RequestId: Connect.makeId(),
            Type: 0
          }
          Connect.send(userKeyRequest)
        }
      }
    }
  })
}

function Socket(props) {
  useEffect(() => {
    let pongInterval

    Connect.onMessage = data => {
      // errors
      if(data.ErrorCode && data.ErrorCode != 0 && data.ErrorCode != 38 && data.ErrorCode != 18 && data.ErrorCode != 15 && data.ErrorCode != 24){
        message.warning(data.Message)

        if (data.ErrorCode === 5) {
          localStorage.removeItem("FileAccessToken")
          localStorage.removeItem("AccessToken")
          localStorage.removeItem("RefreshToken")
          localStorage.removeItem("User")
          localStorage.removeItem("node")
          localStorage.removeItem("keysPair")
          localStorage.removeItem("signKeysPair")
          window.location.reload()
        }
      }
      // pong response
      else if (data.ResponseType === 19 && data.RequestId === 999) {
        clearInterval(pongInterval)
        props.socketReconnect(false)
        
        pongInterval = setInterval(() => {
          Connect.reconnect()
          props.socketReconnect(true)
        }, 5000)
      }
      // Reply to authorization request or token update request
      else if (data.ResponseType === 6) {
        // request information about the authorized user
        const getSelf = {
          RequestId: Connect.makeId(),
          RequestType: 27,
          Type: 0
        }

        Connect.send(getSelf)

        localStorage.setItem("FileAccessToken", data.FileAccessToken)
        localStorage.setItem("AccessToken", data.Token.AccessToken)
        localStorage.setItem("RefreshToken", data.Token.RefreshToken)

        props.setAuth("AUTH_SUCCESS")
      }
      // Response to a request to receive the current user
      else if (data.ResponseType === 12) {
        const user = selectors.getFormatedUser([data.User])
        user[0].currentUser = true
        props.userReceived(user)
        props.toggleModal("close")
        props.toggleDrawer("close")
      }
      // Response to the request for dialogs
      else if (data.ResponseType === 15) {
        props.dialogsReceived(selectors.getFormatedDialogs(data.Conversations))
      }
      // Reply to a message retrieval request
      else if (data.ResponseType === 9) {
        // console.log('odin')
        getUserKeys(data.Messages)
        // second arg - flag for response messages. Only to respond to a message request, you need to set the flag "messagesRecieved" in the dialog
        props.messagesReceived({messages: selectors.getFormatedMessages(data.Messages), isResponse: true})
        // Request.getDialogs()
      }
      // New messages
      else if (data.Message && data.Type === 2) {
        // console.log('dwa')
        props.messagesReceived({messages: selectors.getFormatedMessages([data.Message]), isResponse: false})
        // Request.getDialogs()
      }
      else if (data.ResponseType === 10 && data.ErrorCode === 5) {
        localStorage.removeItem("FileAccessToken")
        localStorage.removeItem("AccessToken")
        localStorage.removeItem("RefreshToken")
        localStorage.removeItem("User")
        localStorage.removeItem("node")
        localStorage.removeItem("keysPair")
        localStorage.removeItem("signKeysPair")
        window.location.reload()
      }
      // Reading outgoing messages
      else if (data.Type === 2 && data.Code === 5) {
        props.readMessages({conversationId: data.ConversationId, conversationType: data.ConversationType})
      }
      // Response to user request
      else if (data.ResponseType === 0) {
        props.userReceived(selectors.getFormatedUser(data.Users))
      }
      // Response to chat requests
      else if (data.ResponseType === 2) {
        const chats = selectors.getFormatedChats(data.Chats)

        // request a list of chat users
        chats.forEach(chat => {
          const getChatsUsers = {
            ChatId: chat.Id,
            NavigationUserId: null,
            RequestId: Connect.makeId(),
            RequestType: 17,
            Type: 0
          }

          Connect.send(getChatsUsers)
        })

        props.chatsReceived(selectors.getFormatedChats(data.Chats))
      }
      // Response to user chat request
      // Their structure is different from ordinary users, here comes information related to this particular chat
      else if (data.ResponseType === 16) {
        props.chatsUsersReceived(selectors.getFormatedChatsUsers(data.ChatUsers))
      }
      // response to a public key request
      else if (data.ResponseType === 18) {
        props.setPublicKeys(data.Keys)
        // const answer = {
        //   data: data.Keys,
        //   id: data.RequestId
        // }
        props.setCallbackData(data)
      }
      // Channel entry response
      else if (data.ResponseType === 21) {
        Request.getDialogs()
      }
      // Search Event Response
      else if (data.ResponseType === 23) {
        const searchResult = {}
        if (data.Users.length > 0) {
          searchResult.users = data.Users
        }
        if (data.Chats.length > 0) {
          searchResult.chats = data.Chats
        }
        if (data.Channels.length > 0) {
          searchResult.channels = data.Channels
        }
        props.searchResultReceived(searchResult)
      }
      // Response to contact list request
      else if (data.ResponseType === 27) {
        let userIDs = data.Contacts.map(user => {
          return user.ContactUserId
        })

        props.setContactUsersIDs(userIDs)
        props.setContacts(data.Contacts)

        if (userIDs.length > 0) {
          Request.getUsers(userIDs)
        }
      }
      // Response to contact group request
      else if (data.ResponseType === 26) {
        props.userGroupsReceived(data.Groups)
      }
      // Message Read Notification
      else if (data.Code === 5) {
        props.messagesRead(data.ConversationId)
      }
      // Request to receive keys from another device
      else if (data.Type === 8 && data.PublicKey) {
        // console.log('      ')
        helpers.devicesKeysExchange(data.PublicKey, data.RequestId)
      }
      // The keys came from another device
      else if (data.Type === 2 && data.Code === 12) {
        // console.log('    ')
        helpers.setKeysFromAnotherDevice(data)
      }
    }
  }, [])

  return null
}

function mapStateToProps(state) {
  return {
    users: state.users
  }
}

const mapDispatchToProps = dispatch => {
  return {
    dialogsReceived: dialogs => dispatch(dialogsReceived(dialogs)),
    chatsReceived: chats => dispatch(chatsReceived(chats)),
    chatsUsersReceived: chatsUsers => dispatch(chatsUsersReceived(chatsUsers)),
    openDialog: dialogId => dispatch(openDialog(dialogId)),
    userReceived: user => dispatch(userReceived(user)),
    messagesReceived: messages => dispatch(messagesReceived(messages)),
    readMessages: data => dispatch(readMessages(data)),
    setAuth: auth => dispatch(setAuth(auth)),
    searchResultReceived: data => dispatch(searchResultReceived(data)),
    setContactUsersIDs: usersIDs => dispatch(setContactUsersIDs(usersIDs)),
    userGroupsReceived: groups => dispatch(userGroupsReceived(groups)),
    setContacts: contacts => dispatch(setContacts(contacts)),
    toggleModal: name => dispatch(toggleModal(name)),
    toggleDrawer: name => dispatch(toggleDrawer(name)),
    setSymmetricKey: key => dispatch(setSymmetricKey(key)),
    setPublicKeys: publicKeys => dispatch(setPublicKeys(publicKeys)),
    setCallbackData: data => dispatch(setCallbackData(data)),
    socketReconnect: data => dispatch(socketReconnect(data)),
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Socket)
