import React, { Component } from "react"
import { Row, Col, Button, Menu, Dropdown, Icon, Select } from "antd"
// import { Link } from "react-router-dom"
import { connect } from "react-redux"
import socket from "../transport/Connect"
import { setActiveNode } from "../actions/node"
import { setPath } from "../actions/path"
// import bigInteger from "big-integer"
// import BigNumber from "bignumber.js"

const { Option } = Select

class Auth extends Component {
  constructor() {
    super()
    // this.state = {
    //   nodes: [],
    //   activeNode: null
    // }
  }

  componentDidMount() {
    // let test = new BigNumber(762147864817264817648172346128746129487162948716249871624986129384761928374691283746)
    // console.log(test)

    // console.log(bigInteger)
    // console.log(bignumber)
    // console.log(jsonBigint)
    // this.setState({
    //   nodes: JSON.parse(localStorage.getItem("nodes")),
    //   activeNode: JSON.parse(localStorage.getItem("node"))
    // })
  }

  selectNode(node) {
    if(node) {
      if(JSON.parse(node).auto) {
        this.props.setActiveNode(this.props.node.allNodes[0])
        localStorage.setItem("node", JSON.stringify(this.props.node.allNodes[0]))
      }
      else {
        let selectedNode = this.props.node.allNodes.find(nod => nod.Id === node)
        if (selectedNode) {
          this.props.setActiveNode(selectedNode)
          localStorage.setItem("node", JSON.stringify(selectedNode))
        }
      }
    }
  }

  connectNode(path) {
    if (this.props.node.activeNode) {
      socket.start(this.props.node.activeNode.Domains[0])
    } else {
      if(this.props.node.allNodes) {
        socket.start(this.props.node.allNodes[0].Domains[0])
        this.props.setActiveNode(this.props.node.allNodes[0])
        localStorage.setItem("node", JSON.stringify(this.props.node.allNodes[0]))
      }
    }
    this.props.setPath(path)
  }

  handleChangeNode(node) {
    if (JSON.parse(node)) {
      connectNode(JSON.parse(node))
      setActiveNode(JSON.parse(node))
      localStorage.setItem("node", node)
    }
  }

  render() {
    return (
      <Row
        type="flex"
        align="middle"
        justify="center"
        style={{ height: "100vh" }}
      >
        <Col>
          <div className="auth__type">
            <h1></h1>
            <h2>  </h2>
            <div className="auth__type_server">
              <span>: </span>
              {
                this.props.node.allNodes ? 
                  <Select defaultValue={localStorage.getItem("node") ? JSON.parse(localStorage.getItem("node")).Id : '{"auto": "true"}'} style={{ width: "100%", overflow: "hidden" }} onChange={this.selectNode.bind(this)}>
                    <Option key={999} value='{"auto": "true"}'></Option>
                    {this.props.node.allNodes.map((node, i) => {
                      // console.log('node - ', node)
                      const nodePhoto = {
                        width: "50px",
                        height: "50px",
                        backgroundSize: "cover",
                        backgroundPosition: "center center",
                        backgroundImage: `url(https://${node.Domains[0]}:5000/api/Files/${node.Photo})`,
                        backgroundRepeat: "no-repeat",
                        borderRadius: "100%",
                        flexShrink: 0
                      }
                      return (
                        <Option key={i} value={node.Id}>
                          <div className='node_photo' style={nodePhoto}></div>
                          <div className='node_fields'>
                            <div><span className='node_field'>: </span><span>{node.Name}</span></div>
                            <div><span className='node_field'>: </span><span>{node.Tag}</span></div>
                            <div><span className='node_field'>: </span><span>{node.Country}</span></div>
                          </div>
                        </Option>
                      )
                    })}
                  </Select>
                : null
              }
            </div>
            <Button type="secondary" onClick={this.connectNode.bind(this, 'by-phone')}>  </Button>
            <br />
            <Button type="secondary" onClick={this.connectNode.bind(this, 'by-email')}>  </Button>
            {/* <br />
            <Button type="secondary"> </Button> */}
            <br />
            <br />
            <Button type="link" onClick={this.connectNode.bind(this, 'registration')}></Button>
          </div>
        </Col>
      </Row>
    )
  }
}

function mapStateToProps(state) {
  return {
    node: state.node
  }
}

const mapDispatchToProps = dispatch => {
  return {
    setActiveNode: node => dispatch(setActiveNode(node)),
    setPath: path => dispatch(setPath(path))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Auth)
