/* eslint-disable */
import Crypto from "y_crypto"
import BigNumber from "bignumber.js"
import socket from "../transport/Connect"
import helpers from "../helpers"

export function setKeysPair() {
  // encryption
  const encoder = new TextEncoder()
  const masterPassword = encoder.encode('qwerty')
  let masterPasswordId = BigNumber(1)
  const crypto = new Crypto(masterPassword, masterPasswordId)

  const setKeyPair = (keys, keysId) => {
    let allKeyPairs = JSON.parse(localStorage.getItem("keysPair")) || []
    let keysInBase64 = keys
    keysInBase64.id = keysId
    keysInBase64.public = helpers.bufferToBase64(keys.public)
    keysInBase64.private = helpers.bufferToBase64(keys.private)
    allKeyPairs.push(keysInBase64)

    localStorage.setItem("keysPair", JSON.stringify(allKeyPairs))
  }

  const setSignKeyPair = (keys, keysId) => {
    let allKeyPairs = JSON.parse(localStorage.getItem("signKeysPair")) || []
    let keysInBase64 = keys
    keysInBase64.id = keysId
    keysInBase64.public = helpers.bufferToBase64(keys.public)
    keysInBase64.private = helpers.bufferToBase64(keys.private)
    allKeyPairs.push(keysInBase64)

    localStorage.setItem("signKeysPair", JSON.stringify(allKeyPairs))
  }

  let cryptoKeysId = socket.makeId()

  // create encryption keys
  crypto.getKeyPairShort(1, cryptoKeysId, 99999, true).then(data => {
    setKeyPair(data, cryptoKeysId)
    cryptoKeysId = socket.makeId()
    crypto.getKeyPairMidle(1, cryptoKeysId, 99999, true).then(data => {
      setKeyPair(data, cryptoKeysId)
      cryptoKeysId = socket.makeId()
      crypto.getKeyPairLong(1, cryptoKeysId, 99999, true).then(data => {
        setKeyPair(data, cryptoKeysId)
      }).catch(err => {console.log(err)})
    }).catch(err => {console.log(err)})
  }).catch(err => {console.log(err)})

  let signKeysId = socket.makeId()

  // create signature keys
  crypto.getKeyPairShort(1, signKeysId, 99999, false).then(data => {
    setSignKeyPair(data, signKeysId)
    signKeysId = socket.makeId()
    crypto.getKeyPairMidle(1, signKeysId, 99999, false).then(data => {
      setSignKeyPair(data, signKeysId)
      signKeysId = socket.makeId()
      crypto.getKeyPairLong(1, signKeysId, 99999, false).then(data => {
        setSignKeyPair(data, signKeysId)
      }).catch(err => {console.log(err)})
    }).catch(err => {console.log(err)})
  }).catch(err => {console.log(err)})
}