/* eslint-disable */
import JSONbig from "json-bigint"
import ReconnectingWebSocket from 'reconnecting-websocket'

class Connect {
  constructor() {
    this.reconnectInterval
    this.needReconnect = true
  }

  start(domain){
    this.webSocket = new ReconnectingWebSocket(`wss://${domain}:5000`)
    // this.webSocket = new WebSocket(`wss://testnode1-1.ymess.org:5000`)

    this.webSocket.onopen = () => {
      // console.log(" ")

      this.needReconnect = true
      clearInterval(this.reconnectInterval)
      this.wsOnConnect()
      
      // ping request for detect connect problem
      setInterval(() => {
        const pingRequest = {
          Length: 1,
          RequestType: 102,
          RequestId: 999,
          Type: 0
        }

        this.send(pingRequest)
      }, 3000)
    }

    this.webSocket.onclose = event => {
      // if (event.wasClean) {
      //   console.log(" ")
      // } else {
      //   // reconnect
      //   console.log(" ")
      //   if(this.webSocket.readyState === 3 && this.needReconnect) {
      //     this.reconnectInterval = setInterval(() => {
      //       console.log(this.webSocket)
      //       // this.webSocket.close(1000, 'changeNode')
      //       // this.webSocket.start(JSON.parse(localStorage.getItem('node')).Domains[0])
      //     }, 3000)
      //   }
      // }
      // console.log(": ", event)
    }

    this.webSocket.onmessage = event => {
      const message = this.blobToJson(event.data)

      if (message.ErrorCode && message.ErrorCode !== 0) {
        // console.log('%c REQUEST ERR ',  'background: #ff4545; color: #bada55; padding: 3px', message)
        this.onMessage(message)
        this.onMessageAuth(message)
        this.onMessageReg(message)
      } else {
        this.onMessage(message)
        this.onMessageAuth(message)
        this.onMessageReg(message)
        if (this.blobToJson(event.data).RequestId != 999) {
          // console.log(
          //   "%c RESPONSE ",
          //   "background: #00ff00; color: #000; padding: 3px",
          //   this.blobToJson(event.data)
          // )
        }
      }
    }

    this.webSocket.onerror = error => {
      // console.log('%c ERROR CONNECT ',  'background: red; color: #bada55; padding: 3px', error);
    }
  }

  close(){
    this.needReconnect = false
    this.webSocket.close(1000, 'changeNode')
  }

  getStatus(){
    return this.webSocket.readyState
  }

  blobToJson(data) {
    var u, x
    u = URL.createObjectURL(data)
    x = new XMLHttpRequest()
    x.open("GET", u, false)
    x.send()
    URL.revokeObjectURL(u)

    return JSONbig.parse(x.responseText)
  }

  makeId() {
    var result = ""
    var characters = "0123456789"
    var charactersLength = characters.length
    for (var i = 0; i < 14; i++) {
      result += characters.charAt(Math.floor(Math.random() * charactersLength))
    }

    return parseInt(result)
  }

  send(data) {
    if (data.RequestId != 999) {
      // console.log(
      //   "%c REQUEST ",
      //   "background: #222; color: #bada55; padding: 3px",
      //   data
      // )
    }
    this.webSocket.send(JSON.stringify(data))
  }

  onMessage() {}

  onMessageAuth() {}

  onMessageReg(){}

  wsOnConnect() {}

  reconnect() {
    // console.log('reconnect')
  }
}

const wSocket = new Connect()

export default wSocket
