/* eslint-disable */
import React, { Component } from "react"
import { Row, Col, Button, Form, Input, Icon, message } from "antd"
import { Redirect } from "react-router-dom"
import { connect } from "react-redux"
import { setAuth } from "../actions/auth"
import { setPrevPath } from "../actions/path"
import socket from "../transport/Connect"
import helpers from "../helpers"

class Verify extends Component {
  constructor() {
    super()
    this.state = {
      verifyUid: null,
      verifyErr: null,
      verifyRequestSended: false,
      verifyCode: null,
      verifyCodeErr: null,
      btnPreloader: false,
      redirect: false
    }
  }

  componentDidMount() {
    socket.onMessageAuth = data => {
      this.setState({
        btnPreloader: false
      })

      if (data.ErrorCode != 0 && data.ErrorCode != 1) {
        if(data.ErrorCode === 18){
          let errMessage = JSON.parse(data.Message)
          if(errMessage.Phone) {
            // setPhoneErr(errMessage.Phone)
          } else if(errMessage.Email) {
            this.setState({
              verifyErr: errMessage.Email
            })
          }
        } else {
          // message.warning(data.Message)
        }

        // this.setState({
        //   verifyErr: data.Message
        // })
      } else if (data.ResponseType === 10) {
        this.setState({
          verifyRequestSended: true
        })
      } else if (data.ResponseType === 6) {
        localStorage.setItem("FileAccessToken", data.FileAccessToken)
        localStorage.setItem("AccessToken", data.Token.AccessToken)
        localStorage.setItem("RefreshToken", data.Token.RefreshToken)
        localStorage.setItem("User", JSON.stringify(data.User))

        this.props.setAuth("AUTH_SUCCESS")

        this.setState({
          redirect: true
        })
      }
    }

    // generate encrypt and sign geys and set the keys in localstorage
    helpers.setKeysPair()
  }

  onChangePhone(e) {
    if(this.props.verifyType === 'by-email') {
      this.setState({
        verifyUid: e.target.value,
        verifyErr: null
      })
    } else if (this.props.verifyType === 'by-phone') {
      const reg = /^-?\d*(\.\d*)?$/
      if ((!isNaN(e.target.value) && reg.test(e.target.value)) || e.target.value === '' || e.target.value === '-') {
        this.setState({
          verifyUid: e.target.value,
          verifyErr: null
        })
      }
    } 
  }

  onChangeVerifCode(e) {
    this.setState({
      verifyCode: e.target.value,
      verifyCodeErr: null
    })
  }

  getOS() {
    if (navigator.appVersion.indexOf('Windows') >= 0) return 'Windows'
    else if (navigator.appVersion.indexOf('Android') >= 0) return 'Android'
    else if (navigator.appVersion.indexOf('Mac') >= 0) return 'MacOS'
    else return "unknown"
  }

  getDevice() {
    if (navigator.userAgent.indexOf("Android") >= 0) return "Phone"
    else if (navigator.userAgent.indexOf("webOS") >= 0) return "webOS"
    else if (navigator.userAgent.indexOf("iPhone") >= 0) return "iPhone"
    else if (navigator.userAgent.indexOf("iPad") >= 0) return "iPad"
    else if (navigator.userAgent.indexOf("iPod") >= 0) return "iPod"
    else return "PC"
  }

  sendverifyCode(e) {
    e.preventDefault()

    this.setState({
      btnPreloader: true
    })

    const data = {
      Uid: this.props.verifyType === 'by-email' ? this.state.verifyUid : `+${this.state.verifyUid}`,
      VCode: this.state.verifyCode,
      LoginType: 1,
      UidType: this.props.verifyType === 'by-email' ? 2 : this.props.verifyType === 'by-phone' ? 0 : 0,
      DeviceName: helpers.getDevice(),
      OSName: helpers.getOS(),
      AppName: "Y messenger - WEB",
      RequestId: socket.makeId(),
      RequestType: 33,
      Type: 0
    }

    socket.send(data)
  }

  sendVerificationQuery(e) {
    e.preventDefault()

    const sendVerifyRequest = () => {
      this.setState({
        btnPreloader: true
      })
  
      const data = {
        VerificationType: this.props.verifyType === 'by-email' ? 1 : this.props.verifyType === 'by-phone' ? 0 : 0,
        Uid: this.props.verifyType === 'by-email' ? this.state.verifyUid : `+${this.state.verifyUid}`,
        RequestId: socket.makeId(),
        RequestType: 42,
        Type: 0
      }

      socket.send(data)
    }

    if(this.props.verifyType === 'by-email') {
      const validateEmail = email => {
        var re = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        return re.test(email);
      }
  
      if (!validateEmail(this.state.verifyUid)) {
        this.setState({
          verifyErr: ' email '
        })
      } else {
        sendVerifyRequest()
        this.setState({
          verifyErr: null
        })
      }
    } else if (this.props.verifyType === 'by-phone') {
      if (this.state.verifyUid.length > 0 && this.state.verifyUid.length != 11) {
        this.setState({
          verifyErr: '    11 '
        })
      } else {
        sendVerifyRequest()
      }
    }
  }

  render() {
    if (this.state.redirect) {
      return <Redirect to="/" />
    } else {
      return (
        <Row
          type="flex"
          align="middle"
          justify="center"
          style={{ height: "100vh" }}
        >
          <Col>
            <div className="auth__type">
              <div className="modal_back" onClick={() => this.props.setPrevPath()}><Icon type="arrow-left" /></div>
              <h1></h1>
              {!this.state.verifyRequestSended ? (
                <div>
                  <h2>
                    {this.props.verifyType === 'by-email' ? '  email' : this.props.verifyType === 'by-phone' ? '  ' : ''}
                  </h2>
                  <Form className="login-form">
                    <Form.Item
                      validateStatus={this.state.verifyErr ? "error" : ""}
                      help={this.state.verifyErr ? this.state.verifyErr : ""}
                    >
                      <Input
                        autoFocus
                        addonBefore={this.props.verifyType === 'by-phone' ? '+' : false}
                        inputMode={this.props.verifyType === 'by-email' ? 'email' : this.props.verifyType === 'by-phone' ? 'tel' : ''}
                        placeholder={this.props.verifyType === 'by-email' ? ' email' : this.props.verifyType === 'by-phone' ? '  ' : ''}
                        onChange={this.onChangePhone.bind(this)}
                        value={this.state.verifyUid}
                        type={this.props.verifyType === 'by-email' ? 'email' : this.props.verifyType === 'by-phone' ? 'phone' : ''}
                      />
                    </Form.Item>
                    <Form.Item>
                      <Row>
                        <Col style={{ textAlign: "center" }}>
                          <Button
                            type="primary"
                            onClick={this.sendVerificationQuery.bind(this)}
                            disabled={!this.state.verifyUid}
                            loading={!!this.state.btnPreloader}
                            htmlType="submit"
                          >
                             {this.props.verifyType === 'by-email' ? '' : this.props.verifyType === 'by-phone' ? 'SMS' : ''}  
                          </Button>
                        </Col>
                      </Row>
                    </Form.Item>
                  </Form>
                </div>
              ) : (
                <div>
                  <h2>   {this.props.verifyType === 'by-email' ? '' : this.props.verifyType === 'by-phone' ? 'SMS' : ''}</h2>
                  <p>
                      {this.props.verifyType === 'by-email' ? ' ' : this.props.verifyType === 'by-phone' ? ' SMS ' : ''} 
                     {this.props.verifyType === 'by-email' ? `email ${this.state.verifyUid}` : this.props.verifyType === 'by-phone' ? ` +${this.state.verifyUid}` : ''}. 
                        
                  </p>
                  <Form
                    onSubmit={this.sendverifyCode.bind(this)}
                    className="login-form"
                  >
                    <Form.Item
                      validateStatus={this.state.verifyCodeErr ? "error" : ""}
                      help={this.state.verifyCodeErr ? this.state.verifyCodeErr : ""}
                    >
                      <Input
                        autoFocus
                        inputMode='tel'
                        placeholder={`  ${this.props.verifyType === 'by-email' ? '' : this.props.verifyType === 'by-phone' ? 'SMS' : ''}, 4 `}
                        onChange={this.onChangeVerifCode.bind(this)}
                      />
                    </Form.Item>
                    <Form.Item>
                      <Row>
                        <Col style={{ textAlign: "center" }}>
                          <Button
                            type="primary"
                            htmlType="submit"
                            disabled={!this.state.verifyCode}
                            loading={!!this.state.btnPreloader}
                          >
                            
                          </Button>
                        </Col>
                      </Row>
                    </Form.Item>
                  </Form>
                </div>
              )}
            </div>
          </Col>
        </Row>
      )
    }
  }
}

function mapStateToProps(state) {
  return {
    verifyType: state.path.currentPath
  }
}

const mapDispatchToProps = dispatch => {
  return {
    setAuth: auth => dispatch(setAuth(auth)),
    setPrevPath: () => dispatch(setPrevPath())
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Verify)
