/* eslint-disable */
import React, { useState, useEffect } from "react"
import { Row, Col, Button, Form, Input, Dropdown, Menu, Icon, message, Select } from "antd"
import { connect } from "react-redux"
import socket from "../transport/Connect"
import helpers from "../helpers"

function Registration(props) {
  const [name, setName] = useState("")
  const [surname, setSurname] = useState("")
  const [phone, setPhone] = useState("")
  const [email, setEmail] = useState("")
  const [verifyCode, setVerifyCode] = useState("")
  const [nameErr, setNameErr] = useState(false)
  const [emailErr, setEmailErr] = useState(false)
  const [phoneErr, setPhoneErr] = useState(false)
  const [contactsErr, setContactsErr] = useState(false)
  const [verifyCodeErr, setVerifyCodeErr] = useState(false)
  const [btnLoader, setbtnLoader] = useState(false)
  const [activeNode, setActiveNode] = useState(null)
  const [showRegButton, setShowRegButton] = useState(false)

  const { Option } = Select

  useEffect(() => {
    socket.onMessageReg = data => {
      if (data.ErrorCode !== 0) {
        if(data.ErrorCode === 18){
          let errMessage = JSON.parse(data.Message)
          if(errMessage.Phone) {
            setPhoneErr(errMessage.Phone)
          } else if(errMessage.Email) {
            setEmailErr(errMessage.Email)
          }
        }
        // else {
        //   message.warning(data.Message)
        // }
        message.warning(data.Message)
      } else if (data.ResponseType === 6) {
        localStorage.setItem("FileAccessToken", data.FileAccessToken)
        localStorage.setItem("AccessToken", data.Token.AccessToken)
        localStorage.setItem("RefreshToken", data.Token.RefreshToken)
        localStorage.setItem("User", JSON.stringify(data.User))

        window.location.replace("/")
      } else if (data.ResponseType === 10) {
          setShowRegButton(true)
      }
    }

    setActiveNode(JSON.parse(localStorage.getItem("node")))

    // generate encrypt and sign geys and set the keys in localstorage
    helpers.setKeysPair()
  }, [])

  const handleRegistration = () => {
    if (!verifyCode) {
      setVerifyCodeErr("    ")
    } else {
      setbtnLoader(true)

      let privacy = []
      for(let i = 0; privacy.length <= 31; i++){
        privacy.push(false)
      }
      privacy[1] = true //name
      privacy[2] = false // photo
      privacy[14] = false // online status
      privacy[15] = true // phone
      privacy[17] = true // email

      const regData = {
        User: {
          Id: 0,
          Phones: phone ? [{FullNumber: `+${phone}`}] : null,
          NameFirst: name,
          NameSecond: surname,
          About: "",
          Photo: "",
          Country: "",
          City: "",
          Birthday: "",
          Language: null,
          NodeId: activeNode.Id,
          Online: null,
          Emails: email ? [email] : null,
          Blacklist: null,
          Visible: null,
          Security: null,
          Tag: 0,
          RegistrationDate: null,
          Privacy: privacy
        },
        VCode: parseInt(verifyCode),
        RequestId: socket.makeId(),
        RequestType: 37,
        Type: 0
      }

      socket.send(regData)
    }
  }

  const handleSetName = e => {
    setName(e.target.value)
    setNameErr(false)
  }

  const handleSetSurname = e => {
    setSurname(e.target.value)
  }

  const handleSetPhone = e => {
    const { value } = e.target
    const reg = /^-?\d*(\.\d*)?$/
    if ((!isNaN(value) && reg.test(value)) || value === '' || value === '-') {
      setPhone(e.target.value)
      setPhoneErr(false)
    } else {
      setPhoneErr('     ')
    }

    setContactsErr(false)
  }

  const handleSetEmail = e => {
    const validateEmail = email => {
      var re = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return re.test(email);
    }

    setEmail(e.target.value)
    setContactsErr(false)

    if (!validateEmail(e.target.value)) {
      setEmailErr(" email ")
    } else {
      setEmailErr(false)
    }

    if (e.target.value.length === 0) {
      setEmailErr(false)
    }
  }

  const handleSetVerifyCode = e => {
    setVerifyCode(e.target.value)
    setVerifyCodeErr(false)
  }

  const connectNode = (node) => {
    if (node) {
      socket.close()
      socket.start(node.Domains[0])
    }
  }

  const handleChangeNode = node => {
    if (node) {
      if(JSON.parse(node).auto) {
        connectNode(props.nodes[0])
        setActiveNode(props.nodes[0])
        localStorage.setItem("node", JSON.stringify(props.nodes[0]))
      }
      else {
        let selectedNode = props.nodes.find(nod => nod.Id === node)
        if (selectedNode) {
          setActiveNode(selectedNode)
          connectNode(selectedNode)
          localStorage.setItem("node", JSON.stringify(selectedNode))
        }
      }
    }
  }

  const getVerifyCode = () => {
    if (name.length === 0) {
      setNameErr("    ")
    } else if (phone.length === 0 && email.length === 0) {
      setContactsErr("      ")
    } else if (phone.length > 0 && phone.length != 11) {
      setPhoneErr('    11 ')
    } else {
      const requestId = socket.makeId()
      const verifyData = {
        VerificationType: email ? 1 : phone ? 0 : 0,
        Uid: phone ? `+${phone}` : email,
        IsRegistration: true,
        RequestId: requestId,
        RequestType: 42,
        Type: 0
      }
      
      setPhoneErr(false)
      socket.send(verifyData)
    }
  }

  return (
    <Row
      type="flex"
      align="middle"
      justify="center"
      style={{ height: "100vh" }}
    >
      <Col>
        <div className="auth__type reg__form">
          <h1></h1>
          <div>
            <div className="auth__type_server">
              <span>: </span>
              <Select defaultValue={localStorage.getItem("node") ? JSON.parse(localStorage.getItem("node")).Id : '{"auto": "true"}'} style={{ width: "100%", overflow: "hidden" }} onChange={handleChangeNode}>
                <Option key={999} value='{"auto": "true"}'></Option>
                {props.nodes.map((node, i) => {
                  const nodePhoto = {
                    width: "50px",
                    height: "50px",
                    backgroundSize: "cover",
                    backgroundPosition: "center center",
                    backgroundImage: `url(https://${node.Domains[0]}:5000/api/Files/${node.Photo})`,
                    backgroundRepeat: "no-repeat",
                    borderRadius: "100%",
                    flexShrink: 0
                  }
                  return (
                    <Option key={i} value={node.Id}>
                      <div className='node_photo' style={nodePhoto}></div>
                      <div className='node_fields'>
                        <div><span className='node_field'>: </span><span>{node.Name}</span></div>
                        <div><span className='node_field'>: </span><span>{node.Tag}</span></div>
                        <div><span className='node_field'>: </span><span>{node.Country}</span></div>
                      </div>
                    </Option>
                  )
                })}
              </Select>
            </div>
            <Form className="login-form">
              {
                showRegButton ? 
                  <>
                    <div className="verify-text">{`       ${phone ? '' : 'email'},         `}</div>
                    <Form.Item
                      validateStatus={verifyCodeErr ? "error" : ""}
                      help={verifyCodeErr || ""}
                    >
                      <Input placeholder="" value={verifyCode} onChange={handleSetVerifyCode} />
                    </Form.Item>
                    <Form.Item>
                      <Row>
                        <Col style={{ textAlign: "center" }}>
                          <Button
                            type="primary"
                            onClick={handleRegistration}
                            loading={btnLoader}
                          >
                            
                          </Button>
                        </Col>
                      </Row>
                    </Form.Item>
                  </>
                :
                  <>
                    <Form.Item
                      validateStatus={nameErr ? "error" : ""}
                      help={nameErr || ""}
                    >
                      <Input placeholder="" onChange={handleSetName} />
                    </Form.Item>
                    <Form.Item validateStatus="">
                      <Input placeholder="" onChange={handleSetSurname} />
                    </Form.Item>
                    {
                      activeNode ? 
                        activeNode.RegistrationMethod === 1 ?
                          <Form.Item
                            validateStatus={contactsErr || phoneErr ? "error" : ""}
                            help={contactsErr || phoneErr || ""}
                          >
                            <Input addonBefore="+" inputMode="tel" type="phone" placeholder=" " value={phone} disabled={email.length ? true : false} onChange={handleSetPhone} />
                          </Form.Item>
                        : activeNode.RegistrationMethod === 2 ? 
                            <Form.Item
                              validateStatus={contactsErr || emailErr ? "error" : ""}
                              help={contactsErr || emailErr || ""}
                            >
                              <Input inputMode="email" type="email" placeholder="Email" disabled={phone.length ? true : false} onChange={handleSetEmail} />
                            </Form.Item>
                        :
                          <>
                            <Form.Item
                              validateStatus={contactsErr || phoneErr ? "error" : ""}
                              help={contactsErr || phoneErr || ""}
                            >
                              <Input addonBefore="+" inputMode="tel" type="phone" placeholder=" " value={phone} disabled={email.length ? true : false} onChange={handleSetPhone} />
                            </Form.Item>
                            <div className="verify-text"><span></span></div>
                            <Form.Item
                              validateStatus={contactsErr || emailErr ? "error" : ""}
                              help={contactsErr || emailErr || ""}
                            >
                              <Input inputMode="email" type="email" placeholder="Email" disabled={phone.length ? true : false} onChange={handleSetEmail} />
                            </Form.Item>
                          </>
                      : null
                    }
                    <Form.Item>
                      <Row>
                        <Col style={{ textAlign: "center" }}>
                          <Button
                            type="primary"
                            onClick={() => getVerifyCode()}
                            disabled={name && phone || email ? false : true}
                            htmlType="submit"
                          >
                             
                          </Button>
                        </Col>
                      </Row>
                    </Form.Item>
                  </>
              }
            </Form>
          </div>
        </div>
      </Col>
    </Row>
  )
}

function mapStateToProps(state) {
  return {
    nodes: state.node.allNodes
  }
}

const mapDispatchToProps = dispatch => {
  return {}
}

export default connect(mapStateToProps, mapDispatchToProps)(Registration)