export function getFormatedUser(data) {
  const users = []
  data.forEach(user => {
    const tempUser = {
      About: user.About,
      Birthday: user.Birthday,
      BlackList: user.BlackList,
      City: user.City,
      Confirmed: user.Confirmed,
      Contact: user.Contact,
      ContactsPrivacy: user.ContactsPrivacy,
      Country: user.Country,
      Emails: user.Emails,
      Encryption: user.Encryption,
      Groups: user.Groups,
      Id: user.Id,
      Keys: user.Keys,
      Language: user.Language,
      NameFirst: user.NameFirst,
      NameSecond: user.NameSecond,
      NodeId: user.NodeId,
      Online: user.Online,
      Phones: user.Phones,
      Photo: user.Photo,
      RegistrationDate: user.RegistrationDate,
      Security: user.Security,
      Tag: user.Tag,
      Visible: user.Visible
    }
    users.push(tempUser)
  })

  return users
}

export function getFormatedChatsUsers(data) {
  const users = []
  data.forEach(user => {
    const tempUser = {
      Banned: user.Banned,
      ChatId: user.ChatId,
      Deleted: user.Deleted,
      InviterId: user.InviterId,
      IsMuted: user.IsMuted,
      Joined: user.Joined,
      UserId: user.UserId,
      UserInfo: user.UserInfo,
      UserRole: user.UserRole
    }
    users.push(tempUser)
  })

  return users
}

export function getUserById(id, users) {
  const foundUser = users.find(user => user.Id + '' === id + '')
  return foundUser
}

export function getUserInitials(item) {
  // console.log(dialog)
  if (!item.Photo) {
    let initials = ""

    if (item.Title || item.ChannelName || item.Name) {
      let defaultAva = item.Title || item.ChannelName || item.Name
      defaultAva = defaultAva.replace(/ +/g, " ").trim()
      defaultAva = defaultAva.split(" ", 2)

      if (defaultAva[0]) {
        initials = defaultAva[0][0]
      }
      if (defaultAva[1]) {
        initials += defaultAva[1][0]
      }
    } else if (item.NameFirst) {
      initials = item.NameFirst[0]
      if (item.NameSecond) {
        initials = initials + item.NameSecond[0]
      }
    } else {
      let uid = item.SecondUid || item.Id
      uid = uid + ""
      initials = uid.substr(0, 2)
    }

    return initials
  }
}

export function getUserName(item, contacts) {
  if (item && contacts) {
    let name = ""
    const userId = item.SecondUid || item.Id
    const contact = contacts.find(user => user.ContactUserId === userId)
    if (contact) {
      if (contact.Name) {
        name = contact.Name
      }
    } else if (item.Title || item.ChannelName || item.Name) {
      name = item.Title || item.ChannelName || item.Name
    } else if (item.NameFirst || item.NameSecond) {
      name = `${item.NameFirst} ${item.NameSecond}`
    } else {
      name = " "
    }

    return name
  }
}
